<template>

	<view class="tab">

		<!-- <tab :lists="lists" :tab_color="tab_color" @switchover="switchover" :gauge="gauge"></tab> -->

		<Goods v-for="(item,index) in searchData" :goodsList="item"></Goods>


	</view>


</template>

<script>
	import Tab from '../../components/Tab.vue';
	import Goods from '../../components/good.vue';
	import {
		fetchSearch
	} from '../../api/index.js'
	export default {
		components: {
			Tab,
			Goods
		},
		data() {
			return {
				//tab切换数组
				lists: [{
						id: 1,
						name: '綜合'
					},
					{
						id: 2,
						name: '銷量'
					},
					{
						id: 3,
						name: '價格'
					},
					{
						id: 4,
						name: '篩選'
					}, {
						id: 5,
						name: '篩選'
					}, {
						id: 6,
						name: '篩選'
					}, {
						id: 7,
						name: '777'
					},

